.block-decorated {
  position: relative;

  &--gray-waves {
    &::before {
      position: absolute;
      content: "";
      width: 100%;
      height: 6px;
      background-image: url("../img/svg/waves-gray-mobile.svg");
      background-repeat: repeat-x;
      bottom: 0;
      left: 0;

      @include tablet {
        height: 17px;
        background-image: url("../img/svg/waves-gray.svg");
      }

      @include desktop {
        width: calc(100% - 100px);
        left: unset;
        height: 23px;
        background-image: url("../img/svg/waves-gray-desktop.svg");
      }
    }
  }

  &--white-waves {
    @include tablet {
      &::after {
        position: absolute;
        z-index: 1;
        content: "";
        width: 100%;
        height: 24px;
        bottom: -1px;
        background-image: url("../img/svg/waves-white.svg");
        background-repeat: repeat-x;
        background-position: center;
      }
    }

    @include desktop {
      &::after {
        background-image: url("../img/svg/waves-white-desktop.svg");
        height: 34px;
      }
    }
  }
}
